body, h1, h2, h3, h4, p, ul {
  margin: 0;
  padding: 0;
}

li {
  list-style: none;
}

h1, h2, h3 {
  font-weight: normal;
}

a {
  text-decoration: none;
}

* {
  box-sizing: border-box;
}

html {
  color: #1c1a26;
  font-family: Inter, sans-serif;
  font-style: normal;
  font-weight: 400;
}

h3 {
  letter-spacing: -4.8px;
  text-transform: uppercase;
  font-size: 96px;
  line-height: 85%;
}

p {
  letter-spacing: -.16px;
  font-size: 16px;
  line-height: 150%;
}

h4 {
  letter-spacing: .32px;
  text-transform: uppercase;
  font-size: 16px;
  font-weight: 500;
  line-height: 100%;
}

h1 {
  letter-spacing: -3.2px;
  text-transform: uppercase;
  font-size: 64px;
  line-height: 85%;
}

@media (min-width: 768px) {
  h1 {
    font-size: 80px;
  }
}

@media (min-width: 1200px) {
  h1 {
    font-size: 136px;
  }
}

@media (min-width: 1444px) {
  h1 {
    font-size: 144px;
  }
}

h2 {
  letter-spacing: -2.4px;
  text-transform: uppercase;
  font-size: 48px;
  line-height: 85%;
}

@media (min-width: 768px) {
  h2 {
    font-size: 56px;
  }
}

@media (min-width: 1200px) {
  h2 {
    font-size: 72px;
  }
}

@keyframes shake {
  0%, 100% {
    transform: translateX(0);
  }

  25% {
    transform: translateX(-1px);
  }

  50% {
    transform: translateX(1px);
  }

  75% {
    transform: translateX(-1px);
  }
}

@font-face {
  font-family: Inter;
  src: url("Inter-Regular.74535f8a.ttf") format("truetype");
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: Inter;
  src: url("Inter-Medium.de3d51e6.ttf") format("truetype");
  font-weight: 500;
  font-style: normal;
}

.page {
  scroll-behavior: smooth;
  background-color: #eef7ff;
}

.page__menu {
  transition: transform .5s;
}

.page__menu:target {
  transform: translateY(50px);
}

.page__section {
  margin-bottom: 72px;
}

@media (min-width: 768px) {
  .page__section {
    margin-bottom: 152px;
  }
}

@media (min-width: 1200px) {
  .page__section {
    margin-bottom: 178px;
  }

  .page__section:last-child {
    margin-bottom: 350px;
  }
}

.container {
  max-width: 1440px;
  margin: 0 auto;
  padding: 0 16px;
}

@media (min-width: 768px) {
  .container {
    padding: 0 44px;
  }
}

@media (min-width: 1444px) {
  .container {
    padding: 0 124px;
  }
}

.grid {
  --gap: 16px;
  column-gap: var(--gap);
  grid-template-columns: repeat(2, 1fr);
  display: grid;
}

@media (min-width: 768px) {
  .grid {
    --gap: 24px;
    grid-template-columns: repeat(8, 1fr);
  }
}

@media (min-width: 1200px) {
  .grid {
    grid-template-columns: repeat(8, 1fr);
  }
}

@media (min-width: 1444px) {
  .grid {
    grid-template-columns: repeat(8, 1fr);
  }
}

.grid--tablet {
  display: block;
